import { Component, OnInit, ViewChild,TemplateRef, HostListener, ViewContainerRef, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { MatDialog } from '@angular/material/dialog';

import { ActivityTestItemComponent } from 'src/app/modules/admin/activity/activity-test/activity-test-item/activity-test-item.component';

import { ActivityTestService } from 'src/app/modules/admin/activity/activity-test/activity-test.service';

import * as moment from 'moment';

@Component({
  selector: 'app-activity-test-item-student',
  templateUrl: './activity-test-item-student.component.html',
  styleUrls: ['./activity-test-item-student.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ActivityTestItemStudentComponent implements OnInit {

  student:any = {}

  activity:any = {}

  @ViewChild("submitTestRef", { static: false }) submitTestRef: TemplateRef<any>;

  constructor(
    private _overlay: Overlay,
    private _viewContainerRef: ViewContainerRef,
    public dialog: MatDialog,
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private _activityHomeworkItemComponent: ActivityTestItemComponent,
    private activityTestService: ActivityTestService
  ) { }

  goBack() {
    this._activityHomeworkItemComponent.matDrawer.close();
    this.activityTestService.setTestActivityStudentData(null)
    this._router.navigateByUrl('/activity/test/list/details/'+this.activity.id);
  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  openSubmitModel() {

    this.dialog.open(this.submitTestRef, {
      panelClass: 'material-dialog-medium'
    });

  }

  submitStudentActivityForm() {

    this.dialog.closeAll()

  }

  ngOnInit(): void {

    this.activityTestService.testactivitystudentdatacast.subscribe((res:any) => {
      if(res != null){
        this._activityHomeworkItemComponent.matDrawer.open();
        this.student = res.data
      }
    })

    this.activityTestService.testactivitydatacast.subscribe((res:any) => {
      if(res != null){
        this.activity = res
      }
    })

  }

}
