import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityTestItemComponent } from './activity-test-item.component';
import { ActivityTestItemStudentComponent } from './activity-test-item-student/activity-test-item-student.component';


const routes: Routes = [
  { path: '', component: ActivityTestItemComponent,
    children: [
      { path: 'student/:studentid', component: ActivityTestItemStudentComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityTestItemRoutingModule { }
